<template>
  <div class="auditions">
    <mt-field label="用户名" placeholder="请输入姓名" v-model="username"></mt-field>
    <mt-field label="手机号" placeholder="请输入手机号" type="number" v-model="phone"></mt-field>
    <mt-field label="qq" placeholder="请输入qq" type="number" v-model="qq"></mt-field>
    <mt-radio
      title="选择想加入的集团"
      v-model="company"
      :options="['华筑集团', '铭世行', '成品家']">
    </mt-radio>
    <div v-show="off">{{array}}</div>
    <mt-button @click="sub" type="danger">提交</mt-button>

  </div>
</template>
<script>
  import { MessageBox } from 'mint-ui';
  export default{
    data(){
      return{
        off:false,
        username:'',
        phone:'',
        qq:'',
        company:'',
        array:[]
      }
    },
    methods:{
      sub(){
        if(this.username && this.phone && this.qq && this.company){
          this.off=true;
          this.array.unshift({
            username:this.username,
            phone:this.phone,
            qq:this.qq,
            company:this.company
          });

          MessageBox.alert('集团申请成功！').then(action => {
            this.$router.push('/home')
          });
        }else{
          this.off=false;
          MessageBox.alert('请输入正确信息！');
        }

      }
    }
  }
</script>
<style>
  .mint-field,.mint-radiolist{
    text-align: left;
    margin-left:1.3333333rem;
  }
  .mint-msgbox-header{
    display: none;
  }
  .mint-msgbox .mint-msgbox-content .mint-msgbox-message{
    font-size:1.8rem;
  }
  .mint-button--danger{
    background: #133562;
    margin-top:1rem;
  }
</style>



